

@media (min-width: 700px) {
  .modalWindowContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    display: none;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
  }

  .modalWindowContainer.open {
    display: flex;
  }

  .modalWindow {
    width: 700px;
    height: 420px;
    background: #FFFFFF;
    border-radius: 5px;
    padding: 30px;
    display: flex;
    position: relative;
  }

  .closeModal {
    position: absolute;
    top: 19px;
    right: 22px;
    cursor: pointer;
    font-size: 25px;
  }

  .imageModal {
    width: 331px;
    height: 205px;
    margin-bottom: 30px;
  }


  .commentFormBlock {
    height: 50%;
  }


  .commentForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }


  .commentButton {
    background: #4997D0;
    border-radius: 3px;
    width: 332px;
    height: 30px;
    border: none;
    color: #FFFFFF;
    outline: none;
    margin: 15px 0;
    cursor: pointer;
  }


  .commentButton:active {
    background: #30668d;
  }

  /*comment block*/
  .commentBlock {
    overflow-y: scroll;
  }

  .comment {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .dateComment {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;
    color: #999999;
  }

  .commentText {
    font-size: 15px;
    line-height: 15px;
    color: #000000;
  }

  /* custom input */
  .photoInputs {
    height: 100%;
    width: 359px;
  }

  .customInput {
    background: #FFFFFF;
    border: 1px solid #CCCCCC;
    box-sizing: border-box;
    border-radius: 3px;
    width: 332px;
    height: 30px;
    margin: 15px 0;
  }
}

@media (min-width: 320px) and (max-width: 700px){
  .modalWindowContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    display: none;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    transition: all 0.5s ease;
    overflow-y: scroll;
  }

  .modalWindowContainer.open {
    display: flex;
  }

  .modalWindow {
    width: 100%;
    height: 100%;
    background: #FFFFFF;
    display: flex;
    position: relative;
  }

  .closeModal {
    position: absolute;
    top: 19px;
    right: 22px;
    cursor: pointer;
    font-size: 25px;
    color: #FFFFFF;
  }

  .photoInputs {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .imageModal {
    width: 100%;
    height: 250px;
    margin-bottom: 25px;
  }

  .commentBlock {
    align-self: flex-start;
    overflow-y: scroll;
    min-height: 150px;
    margin-left: 30px;
    margin-right: 30px;
  }

  .customInput {
    background: #FFFFFF;
    border: 1px solid #CCCCCC;
    box-sizing: border-box;
    border-radius: 3px;
    width: 276px;
    height: 30px;
    margin: 10px 0;
  }

  .comment {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .commentButton {
    background: #4997D0;
    border-radius: 3px;
    width: 276px;
    height: 30px;
    border: none;
    color: #FFFFFF;
    outline: none;
    margin: 5px 0;
    cursor: pointer;
  }

  .dateComment {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;
    color: #999999;
  }

  .commentText {
    font-size: 15px;
    line-height: 15px;
    color: #000000;
  }
}